export function pluralize(
  word: string,
  count?: string | number,
  inclusive?: boolean
) {
  const pluralized = Number(count) === 1 ? word : `${word}s`;

  return (inclusive ? count + ' ' : '') + pluralized;
}

export function capitalize(s: string) {
  return s[0].toUpperCase() + s.substr(1);
}
